import emailjs from '@emailjs/browser';

window.addEventListener("load", function () {
    const elBtnCategories = $('#projects .categories button');
    elBtnCategories.on('click', function (e) {
        e.preventDefault();
        elBtnCategories.removeClass('active');
        $(this).addClass('active');

    });

    new Swiper(".mySwiper", {
        slidesPerView: 3,
        navigation: {
            nextEl: ".swiper-button-next",
            prevEl: ".swiper-button-prev",
        },
    });

    $('#contact .list-btns .btn').on('click', function (e) {
        e.preventDefault();
        $('#contact .list-btns .btn').removeClass('active');
        $(this).addClass('active');
        $('#myForm input[name="selected_option"]').val($(this).text());
    });


    $('#myForm').on('submit', function (e) {
        e.preventDefault();
        $('#myForm .button').addClass('MS__off');
        emailjs.sendForm('service_3343x1e', 'template_68edaxr', '#myForm')
            .then(
                (response) => {
                    console.log('SUCCESS!', response.status, response.text);
                    $('#myForm .button').hide();
                    Fancybox.show([
                        {
                            src: "#popup_inform_2",
                            type: "inline",
                        }
                    ]);
                },
                (error) => {
                    $('#myForm .button').removeClass('MS__off');
                    console.log('FAILED...', error);
                    Fancybox.show([
                        {
                            src: "#popup_inform_1",
                            type: "inline",
                        }
                    ]);
                    
                }
            );
    });
    emailjs.init({
        publicKey: 'uvC1RT8JXl-Q6o2F-',
    });
});
