import * as spine from '@esotericsoftware/spine-webgl';

// Tool SpriteSheet - https://www.leshylabs.com/apps/sstool/
const BASE_URL = ''; // 👈 cấu hình URL tại đây

class Canvas {
    constructor() {
        this.layers = [];
        this.fireFlies = {
            status: true,
            list: [],
            listAmounts: 9,
            wave: 3,
            speed: 1
        };
    };
    addLayer(imagePath, x, y) {
        let newLayer = new Image();
        newLayer.src = imagePath;
        newLayer.onload = () => {
            this.layers.push({ img: newLayer, x, y });
        };
    };
    updateLayerFrameInterval(baseName, layerOrder, newInterval, duration = 1000) {
        const layer = this.layers.find(
            (l) => l.baseName === baseName && l.layerOrder === layerOrder
        );
        if (!layer) return;

        const original = layer.frameInterval;
        layer.frameInterval = newInterval;

        setTimeout(() => {
            layer.frameInterval = original;
        }, duration);
    };
    canvas() {
        this.spawnLoopingV1();
        this.spineWebgl();
        this.spawnElectric();
        this.canvasFireFlies();
    };
    spineWebgl() {
        class App {
            constructor() {
                this.skeleton = null;
                this.animationState = null;
                // Khai báo base URL để tái sử dụng
                this.baseUrl = `${BASE_URL}/assets/profile/images/spin/`;
            }

            loadAssets(canvas) {
                // Load file .skel và .atlas từ base URL
                canvas.assetManager.loadJson(`${this.baseUrl}character_01-cache.json`);
                canvas.assetManager.loadTextureAtlas(`${this.baseUrl}character_01.atlas`);
            }

            initialize(canvas) {
                let assetManager = canvas.assetManager;

                // Tạo texture atlas từ base URL
                var atlas = assetManager.require(`${this.baseUrl}character_01.atlas`);
                var atlasLoader = new spine.AtlasAttachmentLoader(atlas);

                var skeletonJson = new spine.SkeletonJson(atlasLoader);
                // Đọc dữ liệu skeleton

                var skeletonData = skeletonJson.readSkeletonData(
                    assetManager.get(`${this.baseUrl}character_01-cache.json`)
                    // assetManager.require(`${this.baseUrl}character_01-cache.json`)
                );

                // Khởi tạo skeleton và animation
                this.skeleton = new spine.Skeleton(skeletonData);

                this.skeleton.scaleX = 0.9;
                this.skeleton.scaleY = 0.9;
                this.skeleton.y = -335;
                this.skeleton.x = 0;
                var animationStateData = new spine.AnimationStateData(skeletonData);
                this.animationState = new spine.AnimationState(animationStateData);
                this.animationState.setAnimation(0, "animation", true);
            }

            async update(canvas, delta) {
                if ($('.MJ__currentFollowFrame#profile').length > 0) {
                    this.animationState.update(delta);
                };
                this.animationState.apply(this.skeleton);
                this.skeleton.updateWorldTransform(spine.Physics.update);
            }

            render(canvas) {
                let renderer = canvas.renderer;
                // renderer.resize(spine.ResizeMode.Expand);

                // Clear canvas với màu trong suốt (alpha = 0)
                canvas.clear(0, 0, 0, 0);

                renderer.begin();
                renderer.drawSkeleton(this.skeleton, true);
                renderer.end();
            }
        };
        // Khởi tạo ứng dụng
        new spine.SpineCanvas(document.getElementById("spineCanvas"), {
            app: new App(),
            resizeMode: spine.ResizeMode.Fit
        });
    };
    canvasMotionAtlas(options = {}) {
        const {
            element,
            jsonPath,
            imagePath,
            framePrefix = '',
            totalFrames,
            x = 0,
            y = 0,
            scale = 1,
            flip = false,
            frameInterval = 30,
            pause = false,
            loopDelay = 0,
            layerOrder = 0,
            hideWhenPaused = true,
            grayscale = false,
            loop = true // Thêm tham số loop (mặc định là true)
        } = options;

        if (!jsonPath || !imagePath || !totalFrames) {
            console.warn("Missing required parameters: jsonPath, imagePath, totalFrames");
            return;
        };

        // Initialize flag to track if animation is running for this canvas
        if (!this._canvasAnimationStatus) {
            this._canvasAnimationStatus = {};
        };
        // Check if animation is already running for this specific canvas
        if (!this._canvasAnimationStatus[element]) {
            this._canvasAnimationStatus[element] = false;
        };

        Promise.all([
            fetch(jsonPath).then(res => res.json()),
            new Promise(resolve => {
                const img = new Image();
                img.src = imagePath;
                img.onload = () => resolve(img);
            })
        ]).then(([atlas, image]) => {
            const frames = [];

            for (let i = 0; i < totalFrames; i++) {
                const name = `${framePrefix}${i.toString().padStart(2, '0')}`;
                const frameObj = atlas.find(f => f.name === name);

                if (frameObj) {
                    frames.push({
                        x: frameObj.x,
                        y: frameObj.y,
                        w: frameObj.width,
                        h: frameObj.height
                    });
                } else {
                    console.warn(`Missing frame: ${name}`);
                    frames.push(null);
                }
            }

            this.layers.push({
                element,
                frames,
                image,
                x,
                y,
                scale,
                flip,
                frameInterval,
                totalFrames,
                currentFrame: 0,
                lastFrameTime: 0,
                pause,
                loopDelay,
                isWaiting: false,
                lastLoopTime: 0,
                visible: true,
                layerOrder,
                hideWhenPaused,
                spriteSheet: true,
                grayscale // Lưu trạng thái grayscale vào layer
            });

            if (!this._canvasAnimationStatus[element]) {
                this._canvasAnimationStatus[element] = true;
                requestAnimationFrame((timestamp) => this._animate(timestamp, element, grayscale));
            }
        });
    }
    spawnLoopingV1() {
        const loopers = [
            { x: 200, y: -50, scale: 1, flip: false, layerOrder: 1 },
            { x: 620, y: 50, scale: 1, flip: true, layerOrder: 2 },
            { x: 770, y: 430, scale: 0.5, flip: false, layerOrder: 3 },
        ];
        loopers.forEach(({ x, y, scale, flip, layerOrder }, index) => {
            this.canvasMotionAtlas({
                element: 'baseCanvas1',
                jsonPath: `${BASE_URL}/assets/profile/images/canvas/v1.json`,
                imagePath: `${BASE_URL}/assets/profile/images/canvas/v1.png`,
                framePrefix: 'v1_',
                totalFrames: 34,
                x,
                y,
                scale,
                flip,
                frameInterval: 0,
                pause: true,
                hideWhenPaused: false,
                layerOrder,
                grayscale: true
            });
        });
    };
    _animate(timestamp, element) {
        this._drawFrame(timestamp, element);
        requestAnimationFrame((ts) => this._animate(ts, element));
    };
    _drawFrame(timestamp, element) {
        const canvas = document.getElementById(element);
        const ctx = canvas.getContext("2d");
        ctx.clearRect(0, 0, canvas.width, canvas.height);

        const sortedLayers = [...this.layers]
            .filter(layer => layer.element === element) // Chỉ lấy các layer thuộc canvas hiện tại
            .sort((a, b) => a.layerOrder - b.layerOrder);

        sortedLayers.forEach(layer => {
            if (layer.isWaiting) {
                if (timestamp - layer.lastLoopTime >= layer.loopDelay) {
                    layer.isWaiting = false;
                    layer.visible = true;
                    layer.lastFrameTime = timestamp;
                }
                return;
            }

            if ((layer.pause && layer.hideWhenPaused) || !layer.visible) return;

            ctx.save();

            // Kiểm tra và áp dụng bộ lọc grayscale nếu được bật
            if (layer.grayscale) {
                ctx.filter = 'grayscale(100%)';  // Áp dụng bộ lọc grayscale
            } else {
                ctx.filter = 'none';  // Nếu không có grayscale, không áp dụng bộ lọc
            }

            if (layer.spriteSheet) {
                const frame = layer.frames[layer.currentFrame];
                if (!frame) return;
                const { x: sx, y: sy, w, h } = frame;

                if (layer.flip) {
                    ctx.scale(-1, 1);
                    ctx.drawImage(layer.image, sx, sy, w, h, -layer.x - w * layer.scale, layer.y, w * layer.scale, h * layer.scale);
                } else {
                    ctx.drawImage(layer.image, sx, sy, w, h, layer.x, layer.y, w * layer.scale, h * layer.scale);
                }
            } else if (layer.frames) {
                const img = layer.frames[layer.currentFrame];
                if (layer.flip) {
                    ctx.scale(-1, 1);
                    ctx.drawImage(img, -layer.x - img.width, layer.y);
                } else {
                    if (layer.scale !== 1) ctx.scale(layer.scale, layer.scale);
                    ctx.drawImage(img, layer.x, layer.y);
                }
            }

            ctx.restore();

            if (layer.pause) return;

            if (!layer.lastFrameTime || timestamp - layer.lastFrameTime > layer.frameInterval) {
                layer.currentFrame++;

                if (layer.currentFrame >= layer.totalFrames) {
                    layer.currentFrame = 0;

                    if (layer.loopDelay > 0) {
                        layer.visible = false;
                        layer.isWaiting = true;
                        layer.lastLoopTime = timestamp;
                    }
                }

                layer.lastFrameTime = timestamp;
            }
        });
    }
    spawnElectric() {
        this.canvasMotionAtlas({
            element: 'baseCanvas2',
            jsonPath: `${BASE_URL}/assets/profile/images/canvas/electric.json`,
            imagePath: `${BASE_URL}/assets/profile/images/canvas/electric.png`,
            framePrefix: 'electric_',
            totalFrames: 8,
            x: -100,
            y: -270,
            scale: 1.2,
            flip: false,
            frameInterval: 60,
            pause: true,
            layerOrder: 5,
            grayscale: true,
            loopDelay: 500,
        });
    };

    canvasFireFlies() {
        const canvasFirefly = document.getElementById("canvasFirefiles");
        const ctx = canvasFirefly.getContext("2d");

        let w = canvasFirefly.width = window.innerWidth;
        let h = canvasFirefly.height = window.innerHeight;

        class Firefly {
            constructor(speed) {
                this.x = Math.random() * w;
                this.y = Math.random() * h;
                this.s = Math.random() * 2;
                this.ang = Math.random() * 2 * Math.PI;
                this.v = this.s * this.s / 4;
                this.wind = speed;
            };
            move() {
                this.x += this.v * Math.cos(this.ang) + this.wind;
                this.y += this.v * Math.sin(this.ang);
                this.ang += Math.random() * 20 * Math.PI / 180 - 10 * Math.PI / 180;
            };
            show() {
                ctx.beginPath();
                ctx.arc(this.x, this.y, this.s, 0, 2 * Math.PI);
                ctx.fillStyle = "#fddba3";
                ctx.fill();
            };
        };
        const f = this.fireFlies.list;
        const draw = () => {
            if (f.length < this.fireFlies.listAmounts) {
                for (let j = 0; j < this.fireFlies.wave; j++) {
                    f.push(new Firefly(this.fireFlies.speed));
                };
            };
            for (let i = 0; i < f.length; i++) {
                f[i].move();
                f[i].show();
                if (f[i].x < 0 || f[i].x > w || f[i].y < 0 || f[i].y > h) {
                    f.splice(i, 1);
                };
            };
        };
        const loop = async () => {
            if ($('.MJ__currentFollowFrame#profile').length <= 0) {
                return requestAnimationFrame(loop);
            };
            ctx.clearRect(0, 0, w, h);
            if (!this.fireFlies.status) return;
            draw();
            requestAnimationFrame(loop);
        };
        loop();
    };
}
class Core {
    constructor() {
        this.currentTimeout = null;
    };
    wait(timeout) {
        if (this.currentTimeout) {
            clearTimeout(this.currentTimeout);
        }
        return new Promise(resolve => {
            this.currentTimeout = setTimeout(resolve, timeout);
        });
    };
    onEffects() {
        function randomBetween(min, max) {
            return Math.floor(Math.random() * (max - min + 1)) + min;
        };
        const elNav = $('#navigation nav ul li').add($('.hire-btn'));
        const elHover = $('#profile .MS__hover');

        let currentLayer;
        elNav.add(elHover).on('mouseenter', function () {
            if ($('.MJ__currentFollowFrame#profile').length <= 0) return;

            if ($(this).hasClass('special')) {
                canvasInstance.fireFlies.listAmounts = 9 * 6;
                canvasInstance.fireFlies.speed = 6;
                canvasInstance.layers.forEach((layer) => {
                    if (['baseCanvas1', 'baseCanvas2'].includes(layer.element)) {
                        layer.grayscale = false;
                        layer.pause = false;
                    };
                });
            } else {
                canvasInstance.fireFlies.listAmounts = 9 * 3;
                canvasInstance.fireFlies.speed = 3;
                currentLayer = randomBetween(0, 3);
                const layer = canvasInstance.layers[currentLayer];
                if (['baseCanvas1', 'baseCanvas2'].includes(layer.element)) {
                    if (layer.element === 'baseCanvas2') {
                        layer.grayscale = true;;
                    } else {
                        layer.grayscale = false;
                    };
                    layer.pause = false;
                };
            };
            canvasInstance.fireFlies.list.forEach((firefly) => {
                firefly.wind = canvasInstance.fireFlies.speed;
            });
        });
        elNav.add(elHover).on('mouseleave', function () {
            if ($('.MJ__currentFollowFrame#profile').length <= 0) return;

            canvasInstance.fireFlies.listAmounts = 9 * 1;
            canvasInstance.fireFlies.speed = 1;
            canvasInstance.fireFlies.list.forEach((firefly) => {
                firefly.wind = canvasInstance.fireFlies.speed;
            });
            if ($(this).hasClass('special')) {
                canvasInstance.layers.forEach((layer) => {
                    if (['baseCanvas1', 'baseCanvas2'].includes(layer.element)) {
                        layer.grayscale = true;
                        layer.pause = true;
                    };
                });
            } else {
                const layer = canvasInstance.layers[currentLayer];
                if (['baseCanvas1', 'baseCanvas2'].includes(layer.element)) {
                    layer.grayscale = true;
                    layer.pause = true;
                };

            };
        });
    };
    backToTop() {
        const $backToTop = $('.back-to-top');
        const $arrow = $backToTop.find('svg');

        const yellow = 'oklch(85.33% 0.185 92.12)';
        const white = 'oklch(100% 0 0)';
        function updateProgress() {
            const scrollTop = $(window).scrollTop();
            const docHeight = document.documentElement.scrollHeight - window.innerHeight;
            const scrolled = docHeight > 0 ? (scrollTop / docHeight) * 100 : 0;

            // Set background-image trực tiếp
            const gradient = `linear-gradient(${yellow} 0% ${scrolled}%, ${white} ${scrolled}% 100%)`;
            $backToTop.css('background-image', gradient);

            // Xoay mũi tên
            const angle = -90 + scrolled * 1.8;
            $arrow.css('transform', `rotate(${angle}deg)`);
        }

        // Cuộn mượt khi bấm
        $backToTop.on('click', function (e) {
            e.preventDefault();
            $('html, body').animate({ scrollTop: 0 }, 500);
        });

        $(window).on('scroll load', updateProgress);
    }
};
const coreInstance = new Core();
const canvasInstance = new Canvas();
window.addEventListener("load", async function () {
    coreInstance.backToTop();
    if ($('body').data('device-type') == 'desktop') {
        coreInstance.onEffects();
        canvasInstance.canvas();
    };
});
